/*    Menu    */

.ds_none {
    display: none;
}

.ds_none_Block {
    display: block;
   
}

.main_header_content_menu_mobile ul{
    margin-top: 10px;
}

.imgIconMenu{
    width: 40px;
    cursor: pointer;
}

.main_header_content_menu_mobile_obj{
    display: flex;
    align-items: center;
    font-size: 2em;
    padding: 10px;
    color: #FF6500;
    border-radius: 4px 4px 4px 4px;
}

.main_header_content_menu_mobile_obj:hover, .main_header_content_menu_mobile_obj_active{
    cursor: pointer;
}

.tituloMenu {
    color: #FF6500;
    margin: 10px;
    font-size: 1.3em;
    width: 100%;
}

.main_header_content_menu_mobile_sub{
    min-width: 300px;
    background-color: #272727;
    border-radius: 4px 4px 4px 4px;
    justify-content: flex-end;
    flex-wrap: wrap;
    position: absolute;
    border: solid 3px #272727;
} 

.main_header_content_menu_mobile_sub li{
    flex-basis: 100%;
    margin: 20px;
}

.main_header_content_menu_mobile ul li a{
    color: #FF6500;
    text-align: center;
    width: 100%;
    padding: 15px 0;
    text-decoration: none;
}

.main_header_content_menu_mobile ul li a:hover{
    background-color: #303030;
    color: #fff;
}