/*    Main    */
main, .descricaoDoProdutoArticle{
    background-color: #fff;
    max-width: 1300px;
    margin: 20px auto;
    box-shadow: 3px 3px 10px rgba(1, 1, 1, 0.363);
    border-radius: 5px 5px 5px 5px ;
}

.informacoesTecnicasArticle {
    background-color: #fff;
    max-width: 1300px;
    margin: 0px auto;
    box-shadow: 3px 3px 10px rgba(1, 1, 1, 0.363);
    border-radius: 5px 5px 0 0;
}

main header{
    flex-basis: 100%;
}

.nomeDoProdutoH1 {
    flex-basis: 100%;
    padding: 20px 0 10px 20px;
}

.main_conteudo_fotos_textos {
    display: flex;
}

.main_fotos_mini {
    cursor: pointer;
}

.foto1, .foto2, .foto3, .foto4, .foto5, .foto6 {
    width: 80px;
    border: 2px solid #ccc;
    border-radius: 5px;
    margin: 5px 0 5px 20px;
}

.foto6 {
    margin-bottom: 15px;
}

.main_foto_grande {
    width: 530px;
    height: 450px;
}

.main_conteudo_direita {
    width: 600px;
    margin-left: 50px;
}

.precoSemDesconto{
    text-decoration: line-through;
    color: #9C98AE;
}

.precoComDesconto {
    font-size: 2.5rem;
    line-height: 3rem;
    font-weight: 800;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    color: rgb(255, 101, 0);
}

.textosDireita {
    color: #565C69;
    max-width: 650px;
    display: flex;
    flex-wrap: wrap;
}

.QuadradoDireitaCima {
    max-width: 650px;
    height: 82px;
    width: 100%;
    background-color: #DD5700;
    border-radius: 0.5rem 0.5rem 0 0;
    text-align: end;
    padding-top: 30px;
    padding-right: 20px;
    color: #fff;
    font-size: 2em;
}

.QuadradoDireitaBaixo {
    max-width: 650px;
    height: 82px;
    width: 100%;
    background-color: #FF6500;
    border-radius: 0 0 0.5rem 0.5rem;
    margin-top: -30px;
    padding-top: 30px;
    text-align: center;
    padding-right: 20px;
    font-size: 2em;
    color: #fff;
}

.desconto {
    margin-right: 40px;
    color: #fff;
}

.unidades {
    margin-left: 40px;
    color: #fff;
}

.FreteGratis {
    width: 100%;
    text-align: center;
    font-size: 1.5em;
    color: #565C69;
}

.textosESQ {
    max-width: 300px;
}

.bot√£oComprar {
    background-color: #FF6500;
    max-width: 248px;
    height: 48px;
    width: 350px;
    margin-left: 70px;
    margin-top: 20px;
    color: #fff;
    font-weight: 700;
    font-size: 18px;
    padding: 13px;
    border-style: none;
    border-radius: 0.25rem;
    cursor: pointer;
}

.descricaoDoProdutoH2, .informacoesTecnicasH2 {
    padding: 10px 20px 0 20px;
    font-size: 1.8em;
}

.descricao_produto, .informacoesTecnicas{

    font-size: 1.2em;
    margin: 10px 20px 0 20px;
    padding-bottom: 20px;
}